import * as React from "react"
import Layout from "../foundation/layout"
import LowerMv from "../components/lowerMv"
import * as Styles from "../scss/page/gallery.module.scss"
import GalleryFigure from "../components/galleryFigure"
import { graphql,useStaticQuery } from "gatsby"


const GalleryPage = () => {
  const galleryData = useStaticQuery(query);
  return (
    <Layout>
      <LowerMv title="Gallery" />
      <div className={Styles.contents}>
        <div className={Styles.columns}>
        {galleryData.allContentfulGallery.edges.map((edge: any) => {
          return (
            <GalleryFigure
              src={edge.node.figure.gatsbyImageData}
              alt={edge.node.title}
            />
          )
        })}
        </div>
      </div>
    </Layout>
  )
}

export const query = graphql`
  {
    allContentfulGallery {
      edges {
        node {
          title
          figure {
            gatsbyImageData(placeholder: BLURRED)
          }
        }
      }
    }
  }
`

export default GalleryPage